











import Vue from 'vue';
import { mapActions } from 'vuex';

export default Vue.extend({
  name: 'AppControls',
  methods: {
    ...mapActions(['save', 'clear']),
    loadJson(e: InputEvent) {
      const input = e.target as HTMLInputElement;
      if (!input.files) return;
      this.$store.dispatch('load', input.files[0]);
      this.$nextTick(() => {
        input.value = '';
      });
    },
  },
});
